<template>
<div>

    <el-container>
        <el-header>
            <keep-alive>
                <topbar></topbar>
            </keep-alive>
        </el-header>
    </el-container>
    <el-container>
        <el-aside width="250px">
            <keep-alive>
                <leftmenu></leftmenu>
            </keep-alive>
        </el-aside>
        <el-main  class="container_back">
            <el-tabs v-model="activeIndex" type="card" v-if="openTab.length" @tab-click='tabClick' @tab-remove='tabRemove'>
                <el-tab-pane :key="item.name" v-for="(item) in openTab" :label="item.title" :name="item.url" :closable="item.name!='index'">
                </el-tab-pane>
            </el-tabs>
            <transition mode="out-in" name="el-fade-in-linear">
                    <router-view />
                </transition>
        </el-main>
    </el-container>

</div>
</template>

<script>
// import '@/assets/css/main.css'
import "@/assets/css/button.css"
import topbar from '@/components/topbar.vue'
import leftmenu from '@/components/leftmenu.vue'
export default {
    components: {
        topbar,
        leftmenu
    },
    data: () => {
        return {
            editTabValue: "index"
        }
    },
    computed: {
        openTab() {
            return this.$store.getters.get_tabs_openTab;
        },
        activeIndex: {
            get() {
                return this.$store.getters.get_tabs_activeIndex;
            },
            set(val) {
                this.$store.commit('set_active_index', val);
            }
        }
    },
    mounted() {
        this.$store.commit('set_active_index', this.$route.path);
    },
    watch: {
        '$route'(to, from) {
            //判断路由是否已经打开
            //已经打开的 ，将其置为active
            //未打开的，将其放入队列里
            let flag = false;
            this.$store.commit("re_init_page");
            for (let item of this.openTab) {
                if (item.name === to.name) {
                    this.$store.commit('set_active_index', to.path)
                    flag = true;
                    return;
                }
            }
            if (!flag) {
                this.$store.commit('add_tabs', {
                    url: to.path,
                    name: to.name,
                    title: to.meta.title
                });
                this.$store.commit('set_active_index', to.path);
            }
        }
    },
    methods: {
        tabClick(tab) {
            this.$router.push({
                path: this.activeIndex
            });
        },
        //移除tab标签
        tabRemove(targetName) {
            this.$store.commit('delete_tabs', targetName);
            if (this.activeIndex === targetName) {
                // 设置当前激活的路由
                if (this.openTab && this.openTab.length >= 1) {
                    this.$store.commit('set_active_index', this.openTab[this.openTab.length - 1].url);
                    this.$router.push({
                        path: this.activeIndex
                    });
                } else {
                    this.$router.push({
                        path: '/main'
                    });
                }
            }
        }
    },
};
</script>

<style scoped>
.el-header {
    padding: 0;
}
</style>
