<template>
<el-menu :unique-opened="true" text-color="#fff" active-text-color="#fc0"  background-color="#2222" style="margin:none" :router="true" :default-active="defaultActive">
    <el-submenu v-for="(menu,menuindex) in menus" :key="menuindex" :index="`${menuindex}`">
        <template slot="title">
            <i class="el-icon-menu"></i>
            <span>{{menu.pageInf.pagename}}</span>
        </template>
        <el-menu-item-group>
            <el-menu-item :index="`/${list.pageInf.url}`" v-for="(list,index) of menu.list" :key="index" v-text="list.pageInf.pagename">
            </el-menu-item>
        </el-menu-item-group>
    </el-submenu>
</el-menu>
</template>

<script>
export default {
    data: function () {
        return {
            defaultActive:''
        };
    },
    mounted: function () {
        let vm = this;
        vm.loginmenu();
        this.defaultActive = this.$route.path;
    },
    watch: {
        $route(news) {
            this.defaultActive = news.path;
        }
    },
    computed: {
        menus: {
            get() {
                return this.$store.getters.get_user_page;
            },
            set(data) {
                this.menus = data;
            }
        }
    },
    methods: {
        loginmenu() {
            let vm = this;
            if (vm.menus == null || vm.menus.length == 0) {
                vm.$store.dispatch('get_user_menu');
            }
        },
    }
};
</script>

