<template>
<el-menu :router="true" mode="horizontal" background-color="#0002" text-color="#fff" active-text-color="#ffc000" :default-active="defaultActive">
    <el-menu-item index="" class="head-title">AI_BD</el-menu-item>
    <el-submenu class="float-rt" index="">
        <template slot="title">
            <i class="iconfont aibd-icon-user"></i>
            <span>欢迎您，{{user.id}}</span>
        </template>
        <el-menu-item index="/userindex"><i class="iconfont aibd-icon-user"></i>个人中心</el-menu-item>
        <el-menu-item index="">
            <a :href="loginoutaction" @click="loginOut" class="loginout"><i class="el-icon-circle-close-outline"></i>注销</a>
        </el-menu-item>
    </el-submenu>
    <el-menu-item class="float-rt" index="/"><i class="el-icon-setting"></i>设置</el-menu-item>
    <el-menu-item index="/index" class="float-rt"><i class="el-icon-menu"></i>首页</el-menu-item>
</el-menu>
</template>
<script>
export default {
    data() {
        return {
            defaultActive: "/main",
            loginoutaction: `${process.env.VUE_APP_apiUrl}/loginOut.action`,
            user: this.$store.getters.get_user,
        }
    },
    mounted() {
        this.defaultActive = this.$route.path;
    },
    watch: {
        $route(news) {
            this.defaultActive = news.path;
        }
    },
    name: "topbar",
    methods: {
        loginOut() {
            var vm = this;
            vm.$store.commit("set_is_login", false);
            vm.$store.commit("remove_user");
        }
    }
}
</script>

<style scoped>
.head-title {
    font-size: 24px;
}
.loginout{
    display: block;
    width:100%;
    height: 100%;
    color: #ff1201!important;
}
</style>
